import React from 'react';
import { Component } from 'react';
import Tabletop from 'tabletop';
import ReactDOM from 'react-dom';
import Loading from 'react-loading-spinkit';

class Googlesheets extends Component {

	state = {
		isOpen: false,

		data: [],

	}




	componentWillMount() {
		Tabletop.init({
			key: 'https://docs.google.com/spreadsheets/d/1Ganp5BNcwR_LpAygnatA1I36-gB0xRq0gVn3gJSN--w/edit?usp=sharing',
			callback: (data, tabletop) => {
				console.log(data)
				this.setState({ data })
			},
			simpleSheet: true
		})
	}

	render() {


if (this.state.data.length > 0) {
	return (

		<div className="news">

			{
				this.state.data.map(card => (


					<div class="news_card">

							<div class="news_card_info">
									<div class="news_date_title">
										<div class="news_date">
												<p>{card.date}</p>
										</div>
										<div class="news_title">
												<h3>{card.title}</h3>
										</div>
									</div>

									<div class="news_info">
											<p>
												{card.pre_description}
											</p>
											{
												this.state.isOpen &&

											<div className="news_full_info"  >
													<p>
														{card.description}
													</p>
													<ol>
														<li>{card.li_1__item}</li>
														<li>{card.li_2__item}</li>
														<li>{card.li_3__item}</li>
														<li>{card.li_4__item}</li>
														<li>{card.li_5__item}</li>
													</ol>
													<p>
														{card.term}
													</p>
											</div>
													}
									</div>
							</div>

							<div class="news_card_foto">
									<div class="news_foto">
										<img src={card.sourse__img} />
									</div>
									<div class="news_button">
											<a  class="btn " onClick={this.handleClick.bind(this)} > </a>
									</div>
							</div>

				</div>


				))
			}
		</div>
	)
}



		return (
			<div className="preloader">
        <Loading show={true} color='#9c6fc6' name='line-scale-pulse-out' />
      </div>
		)
	}
	handleClick(event) {
		console.log('clicked')
		this.setState({

			isOpen: true,
		})

	}




}

export default Googlesheets;
